<template>
    <div class="container">
        <div class="container-title">商品分类</div>
        <!-- <div class="search-box">
            <div class="search-item">
                <div class="search-item-title">区域名称</div>
                <el-input class="search-item-inp" size="small" v-model="input" placeholder=""></el-input>
            </div>
        </div> -->
        <div class="user-btn">
            <el-button class="user-btn-inp" @click="openModal('add')" type="primary">新增</el-button>
        </div>
        <div class="table-list">
            <el-table ref="singleTable" :data="tableData" style="width: 100%">
                <el-table-column property="id" label="id">
                </el-table-column>
                <el-table-column property="name" label="分类名称">
                </el-table-column>
                <el-table-column fixed="right" label="操作" width="120">
                    <template slot-scope="scope">
                        <el-button @click.native.prevent="editArea(scope.row)" type="text" size="small">
                            编辑
                        </el-button>
                        <el-button @click.native.prevent="deleteArea(scope.row)" type="text" size="small">
                            删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination :current-page="form.pageNumber" layout="prev, pager, next,jumper" :total="total" @current-change="currentChange" :page-size="form.pageSize">
                </el-pagination>
            </div>
        </div>
        <el-dialog :title="`${type == 'add' ? '新增': '编辑'}商品分类`" :visible.sync="dialogVisible" width="width">
            <div>
                <el-form ref="form" :model="form2" label-width="120px">
                    <el-form-item label="商品分类名称" required>
                        <el-input v-model="form2.name"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div slot="footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="areaBtn">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>


<script>
import { shopCategoryCreate, shopCategoryList, shopCategoryDelete, shopCategoryUpdate } from '@/api/shop';
export default {
    data () {
        return {
            tableData: [
                // { title: '活动的名称' }
            ],
            form: {
                pageNumber: 1,
                pageSize: 10,
                isPaging: true,
            },
            form2: {
                name: ''
            },
            dialogVisible: false,
            total: 0,
            type: ''
        };
    },
    created () {
        this.getList();
    },
    methods: {
        currentChange (val) {
            this.form.pageNumber = val;
            this.getList();
        },
        getList () {
            shopCategoryList(this.form).then(res => {
                if (res.data.code == 0) {
                    this.total = res.data.data.amount;
                    this.tableData = res.data.data.rows.map(item => {
                        return { ...item };
                    });
                    const addressList = res.data.data.rows.map(item => {
                        return {
                            label: item.name,
                            value: item.id,
                        };
                    });
                    this.$store.commit('updateAddressList', addressList);
                } else {
                    this.total = 0;
                    this.tableData = [];
                    this.form.pageNumber = 1;
                }
            });
        },
        openModal (type) {
            this.type = type;
            this.dialogVisible = true;
        },
        areaBtn () {
            if (this.type == 'add') {
                this.addArea();
            } else {
                this.upDateArea();
            }
        },
        addArea () {
            shopCategoryCreate({ name: this.form2.name }).then(res => {
                if (res.data.code == 0) {
                    this.$message.success('新增成功');
                    this.getList();
                    this.dialogVisible = false;
                    this.form2.name = '';
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        deleteArea (row) {
            this.$confirm('此操作将永久删除该商品分类, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.areaDelete(row);
            }).catch(() => {

            });

        },
        areaDelete (row) {
            shopCategoryDelete({ id: row.id }).then(res => {
                if (res.data.code == 0) {
                    this.$message.success('删除成功');
                    this.getList();
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        editArea (row) {
            this.type = 'edit';
            this.dialogVisible = true;
            this.form2.name = row.name;
            this.editId = row.id;
        },
        upDateArea () {
            shopCategoryUpdate({ id: this.editId, name: this.form2.name }).then(res => {
                if (res.data.code == 0) {
                    this.$message.success('更新成功');
                    this.dialogVisible = false;
                    this.form2.name = '';
                    this.getList();
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        }
    }
}
</script>

<style scoped>
.list-img {
    width: 200px;
    cursor: pointer;
}
.previewImg-box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.previewImg {
    width: 500px;
}
</style>